.capturePage-container {
  display: flex;
  height: 100vh;
  background-color: #0d67b5; 
  z-index:100
}

.sidebar {
  background-color: #0d67b5;
  color: white;
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center; 
  padding-top: 20px;
}

.sidebar-item {
  display: flex;
  align-items: center;
  width: 80%; 
  padding: 10px 0;
  cursor: pointer;
}

.sidebar-item:hover {
  background-color: #0b5ca4; 
}

.sidebar-item-icon {
  margin-right: 10px; 
}

.sidebar-item-text {
  font-size: 18px;
}

.sidebar-item.active {
  background-color: #0056b3;
}

.logout {
  margin-top: auto; 
}

.content-container {
  flex-grow: 1;
  background-color: white; 
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; 
}

.input-field,
.textarea-field,
.select-dropdown {
  width: 90%;
  max-width: 500px; 
  margin-bottom: 1rem;
  padding: 10px;
  border: 2px solid #ccc; 
  border-radius: 4px; 
  font-size: 1rem;
}

.textarea-field {
  height: 120px; 
  resize: none; 
}

.import-button {
  width: 90%;
  max-width: 500px;
  height: 50px; 
  margin-bottom: 1rem;
  background-color: white; 
  border: 2px dashed #ccc; 
  border-radius: 4px;
  color: #0d67b5; 
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.import-button:hover {
  background-color: #e6e6e6; 
}

.import-icon {
  margin-right: 10px; 
}

.checkbox-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px; 
}

.checkbox-label {
  display: flex;
  align-items: center;
}

.checkbox-custom {
  appearance: none;
  min-width: 20px;
  min-height: 20px;
  border: 2px solid #0d67b5;
  border-radius: 4px;
  transition: background-color 0.3s;
  cursor: pointer;
  position: relative;
}

.checkbox-custom:checked {
  background-color: #0d67b5;
}

.checkbox-custom:checked::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  height: 12px;
  background: white;
}

.submit-button {
  background-color: #0d67b5; 
  color: white;
  border: none;
  padding: 15px 30px; 
  border-radius: 20px; 
  cursor: pointer;
  font-size: 1rem;
  width: auto; 
  max-width: 600px; 
}

.submit-button:hover {
  background-color: #0b5ca4; 
}

.select-dropdown {
  width: 90%;
  max-width: 600px; 
  padding: 10px;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
  background-color: white;
  -webkit-appearance: none; 
  -moz-appearance: none; 
  appearance: none; 
}

.select-dropdown-div::after {
  content: "";
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  border: 5px solid transparent;
  border-top-color: #0d67b5; 
  pointer-events: none;
}

.option {
  padding: 10px;
}

.checkbox-label,
.radio-label {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 1rem;
  user-select: none;
}

.checkbox-label input[type="checkbox"],
.radio-label input[type="radio"] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.checkbox-label .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 4px; 
}

.checkbox-label input[type="checkbox"]:checked ~ .checkmark {
  background-color: #0d67b5;
}

.checkbox-label .checkmark:after {
  content: "";
  display: none;
  position: absolute;
  left: 9px;
  top: 5px;
  width: 8px;
  height: 15px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

.checkbox-label input[type="checkbox"]:checked ~ .checkmark:after {
  display: block;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.sidebar-toggle {
  display: none; 
}



@media (max-width: 768px) {
  .sidebar {
    width: 100%; 
    height: auto; 
    padding: 0.5rem; 
    position: fixed; 
    z-index: 2; 
  }

  .content-container {
    padding-top: 60px; 
  }

  .sidebar-toggle {
    display: block; 
    position: absolute;
    top: 0;
    left: 0;
    padding: 0.5rem;
    z-index: 3; 
  }

  .sidebar-toggle .bar {
    display: block;
    width: 25px; 
    height: 3px; 
    background-color: white; 
    margin: 5px 0; 
    transition: 0.3s; 
  }

  .sidebar-open .sidebar {
    transform: translateX(0); 
  }

  .sidebar-open .sidebar-toggle .bar:nth-child(2) {
    opacity: 0;
  }

  .sidebar-open .sidebar-toggle .bar:nth-child(1) {
    transform: rotate(-45deg) translate(-5px, 6px); 
  }

  .sidebar-open .sidebar-toggle .bar:nth-child(3) {
    transform: rotate(45deg) translate(-5px, -6px); 
  }
}



* {
  box-sizing: border-box;
}

/* Additional global styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
}